import { Component, OnInit, ViewChild } from "@angular/core";
import { EstatesService } from "../services/estates.service";

@Component({
  selector: "app-add-listing-form",
  templateUrl: "./add-listing-form.component.html",
  styleUrls: ["./add-listing-form.component.css"]
})
export class AddListingFormComponent implements OnInit {
  propertyTypes: Array<string> = ["Apartment", "House", "Duplex"];

  constructor(public estatesService: EstatesService) {}

  ngOnInit() {}

  //  Add New Estate
  buttonText: string = "New Estate";

  isNewEstate: boolean = false;

  toggleNewItem(): void {
    this.isNewEstate = !this.isNewEstate;

    if (this.isNewEstate == true) {
      this.buttonText = "Cancel";
    } else {
      this.buttonText = "New Estate";
    }
  }

  onSubmit(data): void {
    this.estatesService.addEstate(data);
  }
}



