import { Component, OnInit } from "@angular/core";
//  Service
import { EstatesService } from "../services/estates.service";
import { UtilService } from "../services/util.service";
//  Model
import { Estate } from "../models/estate";

@Component({
  selector: "app-estate-list",
  templateUrl: "./estate-list.component.html",
  styleUrls: ["./estate-list.component.css"]
})
export class EstateListComponent implements OnInit {
  estates: Estate[];
  error: string;

  sortDirection: string = "asc";

  sortFilter: string = "Sort by";

  sortFilters: string[] = [
    "address",
    "area",
    "bathrooms",
    "bedrooms",
    "price",
    "type"
  ];

  constructor(
    private estateService: EstatesService,
    public utilService: UtilService
  ) {}

  ngOnInit() {
    //  Make a HTTP request
    //  Add HttpModule to app.module.ts, otherwise it will not work
    //  CribsService includes also "Http" and "rxjs/add/operator/map"
    this.estateService
      .getAllEstates()
      .subscribe(data => (this.estates = data), error => (this.error = error));

    this.estateService.newEstateSubject.subscribe(
      data => (this.estates = [data, ...this.estates])
    );
  }

  sortType(type: string) {
    this.sortDirection = type;
    this.sort();
  }

  sort() {
    this.estates.sort((a: any, b: any): any => {
      let x = a[this.sortFilter];
      let y = b[this.sortFilter];

      if (this.sortDirection == "asc") {
        return x - y;
      } else if (this.sortDirection == "desc") {
        return y - x;
      } else {
        return 0;
      }
    });
  }
}



